___CSS_LOADER_EXPORT___.push([module.id, `/**************************/
/******Fonts Import********/
/**************************/ /*Rubik Broken Fax*/ /*Seymour One*/

/**************************/
/*******Tags Style*********/
/**************************/

body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: space-between;
  align-items: center;
}

header {
  background-color: #fe000087;
  box-sizing: border-box;
  width: 100%;
  padding: 5px;
  margin-top: 5px;
  text-align: center;
  font-size: 3rem;
  font-family: "Rubik Broken Fax";
  color: #ffeb3b;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
}

main {
  box-sizing: border-box;
  height: 100%;
  width: 90%;
  margin: 25px;
  display: flex;
  justify-content: space-around;
  background-color: #0000001c;
}

footer {
  color: #ffeb3b;
  font-family: "Seymour One";
  margin-bottom: 10px;
  font-size: x-small;
  word-spacing: 5px;
}

/**************************/
/******Classes Style*******/
/**************************/

.playerContainer,
.aiContainer {
  flex: 2;
  display: flex;
  flex-direction: column;
}

.gameControls {
  flex: 1;
  background-color: rebeccapurple;
}

.playerContainer h1,
.aiContainer h1 {
  font-size: 2rem;
  text-align: center;
  margin: 25px;
  color: yellow;
  font-family: "Rubik Broken Fax";
}

.pG,
.aG {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: repeat(11, 1fr);
  box-sizing: border-box;
  height: 55%;
  margin: 15px;
  background-color: #ffffff54;
  box-shadow: 0px 2px 10px 0px rgb(1 120 166);
}

.nBCell {
  text-align: center;
  align-self: end;
}

.lBCell, .nBCell {
  font-size: large;
  font-weight: 900;
}
